import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  referenceColumn,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMCG } from '../../../../dependencies/empresaMCGDependency';
import { CLASS_NAME, ConfigECFImuneIsenta } from '../../../../models/dtos/mcg/configECFImuneIsenta';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'referencia',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const ConfigECFImuneIsentaSearchView: FC<SearchViewProps<ConfigECFImuneIsenta>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    referenceColumn({
      dataField: 'referencia',
      editable: false,
      sort: true,
      text: 'Referência',
    }),
    {
      dataField: 'total2a7',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Recursos',
    },
    {
      dataField: 'total10a15',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Despesas',
    },
    {
      dataField: 'superavitDeficit',
      editable: false,
      filter: {
        type: FilterType.NUMBER,
      },
      sort: true,
      text: 'Superavit/Déficit',
    },
  ];
  return (
    <WindowComponent
      title="Manutenção SPED ECF - Registro X390"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresaMCG(globalReducer.globalParameter), () => hasEmpresa(globalReducer.globalParameter)]}
      wikiId={207}
    >
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'ConfigECFImuneIsentaEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default ConfigECFImuneIsentaSearchView;
